.activity{
    display: flex;
    flex-direction: column;
    padding: 20px 30px;
    margin: 0px 100px;

    @include respond-to('small') {
        margin: 0px;
        padding: 20px;
    }

    &__title{
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 15px;

        &Line{
            width: 100%;
            height: 2px;
            background-color: #E8E8EE;  
        }

        & Button{
            width: 47px;
            height: 32px;
            border-radius: 5px;
            background-color: rgba(157,155,182,0.17);
            padding: 15px;

            @include respond-to('small') {
                display: none;
            }
        }
    }

    &__cards{
        display: flex;
        flex-direction: column;
        width: 100%;
        margin-top: 30px;
        gap: 15px;

        &Title{
            display: flex;
            flex-direction: row;
            
            & p:nth-child(2) {
                margin-left: 5px;
            }

            @include respond-to('small') {
                display: block;

                & p:nth-child(2) {
                    margin-left: 0px;
                }
            }

            
        }
    }
}